import React from 'react';
import ReactDOM from 'react-dom';
import Cards from "./Cards";
import Sdata from "./Sdata";
import "./index.css"

function ncard(val)
{
  return (
    <Cards
    imgsrc={val.imgsrc}
    title={val.title}
    sname={val.sname}
    link={val.links
    }
    />
  );
}
ReactDOM.render(
 <>
<h1 className="heading_style">MY FAVOURITE LIST OF TOP 6 NETFLIX SERIES</h1>
{Sdata.map(ncard)};
<footer>
    <p>Created By Shiva</p>
</footer>
 </>,
 document.getElementById("root")
);

